// @media (max-width: $width-max-mobile) {}
// @media (min-width: $width-tablet) {}
// @media (min-width: $width-desktop) {}

/// 
$width-mobile: 320px;
$width-tablet: 660px;
$width-desktop: 1200px;

///  ,        
$width-max-mobile: (
  $width-tablet - 1
);

///    
$width-mobile-padding: 20px;
$width-tablet-padding: 20px;
$width-desktop-padding: 130px;

///   
///   = 280px
$width-mobile-content: (
  $width-mobile - 2 * $width-mobile-padding
);

///   = 620px [: 2 * 300px + 20px]
$width-tablet-content: (
  $width-tablet - 2 * $width-tablet-padding
);

///   = 940px [: 3 * 300px + 2 * 20px]
$width-desktop-content: (
  $width-desktop - 2 * $width-desktop-padding
);

///        
/// (..    )
$width-pre-desktop: (
  $width-desktop-content + 100px
);

//   
$width-adaptive: calc(100vw - 2 * 130px);
$width-mobile-adaptive: calc(100vw - 2 * $width-mobile-padding);
$width-tablet-adaptive: calc(100vw - 2 * $width-tablet-padding);

/// 
$font-title: "Open Sans", "Verdana", sans-serif;
$font-body: "Open Sans", "Verdana", sans-serif;

/// 

$color-gray--de: #dedede;
$color-gray--e5: #e5e5e5;
$color-gray--f2: #f2f2f2;
$color-black: #000000;
$color-white: #ffffff;
$color-primary: #283645;
$color-primary-dark: #1d2631;
$color-secondary: #d22856;
$color-canvas: $color-gray--f2;
$color-form_button: #2aab6d;
$color-form_button--hover: #1c9b5e;
$status-error: #ff5d5d;
$status-focus: #1c9b5e;
$opacity: 0.3;
