// .gallery {}

.gallery__lead {
  @media (max-width: $width-max-mobile) {
    padding-top: 44px;
    padding-bottom: 50px;

    & > br {
      display: none;
    }
  }

  @media (min-width: $width-tablet) {
    padding-top: 92px;
    padding-bottom: 76px;
  }

  @media (min-width: $width-desktop) {
    padding-top: 92px;
    padding-bottom: 88px;
  }
}

.gallery__list-wrapper {
  position: relative;
}

.gallery__list {
  display: grid;
  gap: 20px;
  margin: 0 auto;
  padding: 0;
  list-style-type: none;

  @include adaptiveWidth();

  @media (min-width: $width-tablet) {
    grid-template-columns: 1fr 1fr;
  }

  @media (min-width: $width-desktop) {
    grid-template-columns: repeat(3, 1fr);
    row-gap: 52px;
  }
}

.gallery__item {
  &--large {
    @media (min-width: $width-tablet) {
      //    50px     
      // : margin-bottom: 30px + row-gap: 20px;
      margin-bottom: 30px;
      grid-column: 1 / -1;
    }

    @media (min-width: $width-desktop) {
      margin-bottom: 0;
    }
  }

  // #region  -
  //   ()      ,
  //         ,   
  //    ,      .
  // #endregion

  &--large::before {
    content: " ";
    position: absolute;
    top: 0;
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 140px;
    height: 36px;
    font-size: 12px;
    font-weight: 700;
    color: $color-white;
    background-color: $color-secondary;

    @media (max-width: $width-max-mobile) {
      right: calc((100vw - $width-mobile) / 2 + $width-mobile-padding);
    }

    @media (min-width: $width-tablet) {
      top: 57px;
      right: calc((100vw - $width-tablet) / 2 + $width-tablet-padding);
    }

    @media (min-width: $width-desktop) {
      top: 54px;
      right: calc((100vw - $width-desktop) / 2 + $width-desktop-padding);
    }
  }
}

.gallery__card {
  display: flex;
  flex-direction: column;
  height: 100%;
  padding-bottom: 19px;
  background-color: $color-canvas;

  @media (min-width: $width-tablet) {
    min-height: 455px;
    padding-bottom: 24px;
  }

  @media (min-width: $width-desktop) {
    min-height: 555px;
    padding-bottom: 23px;
  }

  //  () 
  .gallery__item--large & {
    @media (max-width: $width-max-mobile) {
      // #region   
      //    (h)       1px   ,     .    -     .      : 320 / 137 = 2.336  ,      2.336  .        1px,     : h=(w + 1) / 2.336  w -       1px.  ,      320px  321px,    : h=(321 / 2.336)=137.25px - ,   -    ,         .           .  ,      137px.
      // #endregion

      // 137px + 18px (  +    )
      // (100vw - $width-mobile) / 2.336 -            

      padding-top: max(155px, calc((100vw - $width-mobile) / 2.34 + 137px + 18px));
    }

    @media (min-width: $width-tablet) {
      flex-direction: row;
      flex-wrap: wrap;
      // 509px + 23px (  +    )
      padding-top: (509px + 23px);
    }

    @media (min-width: $width-desktop) {
      // 506px + 24px (  +    )
      padding-top: (506px + 23px);
    }
  }
}

.gallery__card-image {
  display: block;
  width: 100%;
  margin-bottom: 17px;

  @media (min-width: $width-tablet) {
    height: 300px;
    margin-bottom: 23px;
  }

  @media (min-width: $width-desktop) {
    height: 400px;
    margin-bottom: 24px;
  }

  .gallery__item--large & {
    position: absolute;
    top: 0;
    left: 0;

    @media (min-width: $width-tablet) {
      height: 509px;
    }

    @media (min-width: $width-desktop) {
      height: 506px;
    }
  }
}

.gallery__card-footer,
.gallery__card-describe,
.gallery__card-likes {
  padding: 0 17px;

  @media (min-width: $width-tablet) {
    padding: 0 30px;
  }

  @media (min-width: $width-desktop) {
    padding: 0 27px;
  }
}

.gallery__card-name {
  font-weight: 700;
}

.gallery__card-date {
  font-size: 12px;
  font-weight: 300;
  text-transform: uppercase;
}

//   
.gallery__card-describe {
  margin: 0;
  margin-bottom: 11px;

  @media (min-width: $width-tablet) {
    margin-bottom: 12px;
  }

  //    #1
  .gallery__item--large & {
    @media (min-width: $width-tablet) {
      flex-grow: 1;
      order: 3;
    }

    @media (min-width: $width-tablet) {
      margin-bottom: 0;
    }
  }
}

//   
.gallery__card-likes {
  display: flex;
  align-items: center;
  column-gap: 18px;
  margin: 0;
  margin-top: auto;

  @media (min-width: $width-tablet) {
    flex-direction: row-reverse;
    justify-content: space-between;
  }

  //    #1
  .gallery__item--large & {
    @media (min-width: $width-tablet) {
      margin-left: auto;
    }
  }
}

//  
.gallery__card-likes-info {
  order: 2;
  color: $color-secondary;
  font-size: 12px;
  font-weight: 400;
  text-transform: uppercase;
}

//  
.gallery__card-likes-button {
  padding: 0;
  border: 0;
  width: 13px;
  height: 12px;
  background-color: transparent;
  background-image: url("../img/ico/heart.svg");
  background-repeat: no-repeat;

  &:hover {
    cursor: pointer;
  }

  &:focus {
    outline-offset: 5px;
  }
}
